:root {
  --primary-white: #fff;
  --primary-green: #03c03c;
  --primary-black: #000;
}

body {
  overflow: hidden;
}

.btn-siteprim {
  background-color: var(--primary-green) !important;
  color: var(--primary-white) !important;
  border: none;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
}

.btn-siteprim:hover {
  background-color: var(--primary-black) !important;
}

.text-prim{
  color: var(--primary-green);
}

/* Navbar */
nav {
  z-index: 2;
}

#portfolio-text-splitted, #github-logo, #linkedin-logo, .navbar-icons {
  fill: var(--primary-white)
}

.nav-link .nav-link-text {
  color: var(--primary-white);
}

.nav-link .navbar-icons {
  fill: var(--primary-white);
}

.nav-link:hover .navbar-icons, .nav-link:hover svg .site-logo {
  fill: var(--primary-green) !important;
}

.nav-link:hover .nav-link-text {
  color: var(--primary-green);
}


@media (min-width: 992PX) {
  .navbar-brand.abs {
    position: absolute;
    width: auto;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    text-align: center;
  }

  .navbar-item-text {
    display: none;
  }
}

#nav-brand-name-f {
  -webkit-animation: change-color-f 3s infinite;
          animation: change-color-f 3s infinite;
}

.navbar-expand .navbar-item-text {
  display: none;
}

@-webkit-keyframes change-color-f {
  0% {
    fill: var(--primary-white);
  }
  10% {
    fill: var(--primary-green);
  }
  20% {
    fill: var(--primary-white);
  }
  80% {
    fill: var(--primary-white);
  }
  90% {
    fill: var(--primary-green);
  }
  100% {
    fill: var(--primary-white);
  }
}

@keyframes change-color-f {
  0% {
    fill: var(--primary-white);
  }
  10% {
    fill: var(--primary-green);
  }
  20% {
