/* Header */
.header-wrapper {
  position: relative;
  background: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url("./images/pexels-pixabay-270360.jpg") no-repeat;
  background-size: cover;
  background-position: center;
  height: 95vh;
}

.main-info {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  z-index: 1;
}

.main-info h1 {
  color: var(--primary-green);
}

/* Typed Text */
.typed-text {
  font-size: 2rem;
  color: var(--primary-white);
}

/* Particles */
.tsparticles-canvas-el {
  position: absolute;
  height: 95vh;
  width: 100vw;
  z-index: 1;
}

/* About */

.introduction {
  color: var(--primary-green);
}

/* Skills */

#prog-lang li {
  padding: 0;
  list-style: none;
  font-size: 30px;
}

/* Projects */

.card {
  width: 10%;
}

.card:hover {
  background-color: var(--primary-green);
  color: var(--primary-white);
}

.card-btn {
  background: transparent;
  border: none !important;
  font-size: 0;
}

.card-btn:focus {
  outline: none;
  box-shadow: none;
}

.modal-dialog {
  width: 75vw;
}